<template>
  <div id="appraise">
    <div class="lunboye" v-show="!lunboShow" @click="lunbShowF" style="background: rgba(0,0,0,.8);width:100%;height:100%;position: fixed;top:0;left:0;display: flex;align-items: center;justify-content: center">
      <div class="details_banner">
        <img class="preview-img " :src="ites"/>
        <!--<mt-swipe :auto="0" :show-indicators="false">-->
          <!--<mt-swipe-item v-for="(ite,indexm) in totalPico"  :key="indexm">-->
            <!--<img class="preview-img " :src="ite"/>-->
          <!--</mt-swipe-item>-->
        <!--</mt-swipe>-->
      </div>
    </div>
    <header class="mui-bar mui-bar-nav" style="box-shadow: none;">
      <a class="mui-icon mui-icon-left-nav mui-pull-left" @click="goback"></a>
      <h1 class="mui-title">评价</h1>
    </header>
    <div class="appraise_header">
      <ul class="clearfix" ref="appraisepart">
        <li>
          <a href="javascript:;" @click.stop="loadPageListall">全部 ({{allcount}})</a>
        </li>
        <li >
          <a href="javascript:;" @click.stop="loadPageListgood">好评 ({{goodcount}})</a>
        </li>
        <li >
          <a href="javascript:;" @click.stop="loadPageListmiddle">中评 ({{midcount}})</a>
        </li>
        <li>
          <a href="javascript:;" @click.stop="loadPageListbad">差评 ({{badcount}})</a>
        </li>
        <li >
          <a href="javascript:;" @click.stop="loadPageListpic">有图 ({{hascount}})</a>
        </li>
      </ul>
    </div>
    <div class="appraiseNew">最新评论</div>
    <div class="page-loadmore">
        <div class="page-loadmore-wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
          <mt-loadmore :bottom-method="loadBottom" @bottom-status-change="handleBottomChange" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
            <div class="pretermission" style="font-size: 0.4rem;color:#000;line-height:2rem;" v-if="!blank">目前还没有留言哦</div>
            <div id="appraise_deatil">
              <ul  v-if="blank" class="list">
                <li class="comment_main" v-for="(item,index) in proCopyright"  :key="index">
                  <div class="comment_mainContent">
                    <h2 >
                      <strong><img :src="item.userAvatar" alt=""/></strong>
                      <span class="comment_main_name">{{item.userName | mstr}}</span>
                      <span class="comment_main_time">{{item.createTime | formatTime('YMDHMS')}}</span>
                    </h2>
                    <div class="comment_main_texts">
                      <p>{{item.commentText}}</p>
                      <ul class="clearfix" v-if="item.haspic==1?false:true">
                        <li  v-for="(val,indexs) in item.picNum" :key="indexs"><img :src='item[`skuPic${val}`]' alt="" @click="isMask(index,indexs)" /></li>
                      </ul>
                      <div>
                        <p>{{item.skuText}}</p>
                      </div>
                    </div>
                  </div>
                </li>
              </ul>
            </div>
          </mt-loadmore>
        </div>
      </div>
  </div>
</template>
<script >
  import Api from '../commom/API.js';
  import {Loadmore} from 'mint-ui';
  export default {
    data(){
      return {
        lunboShow: true,
        goodid:this.$route.query.selected,
        allcount: 0,
        badcount: 0,
        goodcount: 0,
        midcount: 0,
        badcount: 0,
        hascount: 0,
        blank:false,
        pageNo:0,
        proCopyright:[],
        allLoaded: false, //是否可以上拉属性，false可以上拉，true为禁止上拉，就是不让往上划加载数据了
        scrollMode:"auto", //移动端弹性滚动效果，touch为弹性滚动，auto是非弹性滚动
        totalpage:"",//总共多少页
        loading:false,
        bottomStatus: '',
        wrapperHeight: 0,
        translate: 0,
        moveTranslate: 0,
        level: 0,
        haspic:0,
        totalPic:[],
        totalPico:[],
        maskShow:0,
        ites:''
      }
    },
    mounted(){
      this.loadPageListall()
      this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
    },
    watch:{
      $route(to){
        var path=to.path.substring(1);
        if(path.indexOf(this.$route.params.id)!=-1){
          this.loadPageList()
        }
      }
    },
    methods: {
      lunbShowF(){
        this.lunboShow = true
      },
      isMask(index,indexs){
        this.lunboShow = false
        this.totalPico=this.totalPic[index]
        this.maskShow=indexs
        this.ites=this.totalPic[index][indexs]
      },
      addactive(){
        for(var i = 0;i<this.$refs.appraisepart.children.length;i++){
          this.$refs.appraisepart.children[i].className= ''
        }
      },
      loadPageListall(){
        this.totalPic=[]
        this.allLoaded = false;
        this.pageNo = 0;
        this.level = 0;
        this.haspic = 0;
        this.addactive();
        this.$refs.appraisepart.children[0].className="active";
        this.loadPageList()
      },
      loadPageListgood(){
        this.totalPic=[]
        this.allLoaded = false;
        this.pageNo = 0;
        this.level = 3;
        this.haspic = 0;
        this.addactive();
        this.$refs.appraisepart.children[1].className="active";
        this.loadPageList()
      },
      loadPageListmiddle(){
        this.totalPic=[]
        this.allLoaded = false;
        this.pageNo = 0;
        this.level = 2;
        this.haspic = 0;
        this.addactive();
        this.$refs.appraisepart.children[2].className="active";
        this.loadPageList()
      },
      loadPageListbad(){
        this.totalPic=[]
        this.allLoaded = false;
        this.pageNo = 0;
        this.level = 1;
        this.haspic = 0;
        this.addactive();
        this.$refs.appraisepart.children[3].className="active";
        this.loadPageList()
      },
      loadPageListpic(){
        this.totalPic=[]
        this.allLoaded = false;
        this.pageNo = 0;
        this.level = 0;
        this.haspic = 2;
        this.addactive();
        this.$refs.appraisepart.children[4].className="active";
        this.loadPageList()
      },
      handleBottomChange(status) {
        this.bottomStatus = status;
      },
      //上拉加载
      loadBottom:function() {
        this.totalPic=[]
        setTimeout(() => {
          var _this = this;
          if (this.pageNo < this.totalpage) {
            let data = {'haspic':this.haspic,'index':this.pageNo,'goodid':this.$route.query.selected,'level':this.level};
            var url = Api.Apis + '/h5/h5goodcomment/getComment';
            this.$http.post(url, data, {emulateJSON: true}
            ).then((res) => {
              var bigData = res.data;
              _this.allcount=bigData.allcount;
              _this.goodcount=bigData.goodcount;
              _this.midcount=bigData.midcount;
              _this.badcount=bigData.badcount;
              _this.hascount=bigData.hascount;
              if(bigData.comments.length ==0){
                _this.allLoaded = true;
                return false
              }else{
                _this.totalpage = bigData.maxIndex;
                bigData.comments.forEach((value)=>{_this.proCopyright.push(value)});
                for(let i=0;i<_this.proCopyright.length;i++){
                  _this.totalPic.push([])
                  if(_this.proCopyright[i].haspic==2){
                    for(let j=0;j<_this.proCopyright[i].picNum;j++){
                      _this.totalPic[i].push(_this.proCopyright[i][`skuPic${j+1}`])
                    }
                  }
                }
                console.log('_this.totalPic',_this.totalPic,'_this.proCopyright',_this.proCopyright)
                _this.pageNo++
                if (_this.pageNo >=_this.totalpage) {
                  _this.allLoaded = true;
                }
              }
            });
          } else {
            _this.allLoaded = true;
          }
          _this.$refs.loadmore.onBottomLoaded();
        }, 1500);
      },
      //请求数据
      loadPageList:function () {
        var _this = this;
        var url = Api.Apis + '/h5/h5goodcomment/getComment';
        this.$http.post(url, {
          'haspic': this.haspic,
          'index': this.pageNo,
          'goodid': this.$route.query.selected,
          'level': this.level
        }, {emulateJSON: true}).then(function (res) {
          var bigData = res.data;
          _this.allcount = bigData.allcount;
          _this.goodcount = bigData.goodcount;
          _this.midcount = bigData.midcount;
          _this.badcount = bigData.badcount;
          _this.hascount = bigData.hascount;
          if (bigData.comments.length == 0) {
            _this.blank = false;
            this.allLoaded = true;
            return false
          } else {
            _this.blank = true;
            _this.proCopyright = bigData.comments;
            _this.totalpage = bigData.maxIndex;
            _this.pageNo++
            for(let i=0;i<_this.proCopyright.length;i++){
              _this.totalPic.push([])
              if(_this.proCopyright[i].haspic==2){
                for(let j=0;j<_this.proCopyright[i].picNum;j++){
                  _this.totalPic[i].push(_this.proCopyright[i][`skuPic${j+1}`])
                }
              }
            }
            if (this.totalpage == 1) {
              this.allLoaded = true;
            }
          }
        })
      },
     goback(){
      this.$router.go(-1);
     }
    }
  }

</script>
<style  scoped>
  .page-loadmore{
    background: #e5e5e5;
  }
  img{
    width:100%;
    height:100%;
  }
  .details_banner{
    margin-top: 0.75rem;
    height: 6.30rem;
    width: 100%;
    position: relative;
  }
  mint-loadmore-top span {
    -webkit-transition: .2s linear;
    transition: .2s linear
  }
  .mint-loadmore-top span {
    display: inline-block;
    vertical-align: middle
  }
  .page-loadmore-wrapper {
    overflow: scroll
  }
  .mint-loadmore-bottom span {
    display: inline-block;
    -webkit-transition: .2s linear;
    transition: .2s linear;
    vertical-align: middle
  }
  .clearfix:after { content: ""; display: block; clear: both; }
  .clearfix { zoom: 1; }
  #appraise{
    background: #fff !important;
  }
  .mui-bar.mui-bar-nav,.appraiseNew,.appraise_header ul li{
    background-color: #e5e5e5;
  }
  .mui-bar .mui-title{
    font-size: 0.44rem;
  }
  a.mui-icon{
    color: #007aff;
  }
  .appraiseNew{
    height: 1.2rem;
    padding-top: 0.6rem;
    padding-left: 0.3rem;
    color: #999;
    font-size: 0.35rem;
    text-align: left;
  }
  .appraise_header{
    width: 100%;
    margin-top: 0.88rem;
    padding-left: 0.2rem;
    padding-top: 0.2rem;
    padding-bottom: 0.2rem;
    height: 2.1rem;
  }
  .appraise_header ul li{
    float: left;
    width: 26%;
    height: 0.7rem;
    margin-right: 0.36rem;
    margin-bottom: 0.36rem;
    font-size: 0.34rem;
    text-align: center;
    line-height: 0.7rem;
    color: #000;
  }
  .appraise_header ul li a{
    width: 100%;
    display: inline-block;
  }
  .appraise_header ul li.active a{
    color: #fff;
    background: #4595e5;
  }
  #appraise_deatil .list{
    overflow: hidden;
  }
  .comment_main{
    margin-bottom: 0.18rem;
    background: #fff;
    overflow: hidden;
  }
  .comment_mainContent{
    padding: 0 0.3rem;
  }
  .comment_main h2{
    height: 1.1rem;
    line-height: 1.11rem;
    font-weight: normal;
    margin-top: 0.1rem;
    margin-bottom: 0.1rem;

  }
  .comment_main h2 strong{
    display: inline-block;
    float: left;
    width: 0.7rem;
    height: 0.7rem;
  }
  .comment_main h2 strong img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
    vertical-align: middle;
    border: 0;
  }
  .comment_main h2 span{
    float: left;
    font-size: 0.4rem;
    color: #999999;
    padding-left: 0.2rem;
    font-size: 0.26rem;
    line-height: 1.11rem;
  }
  .comment_main h2 span.comment_main_time{
    float: right;
  }
  .comment_main .comment_main_texts p{
    text-align: left;
    line-height: 0.42rem;
    font-size: 0.27rem;
  }
  .comment_main .comment_main_texts div p{
    color: #999;
    text-align: left;
    line-height: 0.42rem;
    font-size: 0.27rem;
  }
  .comment_main_texts li{
    float: left;
    width:1.26rem;
    height:1.26rem;
    margin-right:0.06rem;
    margin-left:0.06rem;

  }
  .comment_main_texts li img{
    display: block;
    width: 100%;
    height: 100%;
  }
</style>





