<template>
  <div id="classify">
    <header class="header mui-bar mui-bar-nav" style="background: #fff;box-shadow: none">
        <router-link to="/seek" tag="p" class="seeker"><span class="mui-icon mui-icon-search"></span><p>从这里出发，你会了解更多</p></router-link>
    </header>
    <div class="mui-content classifymain" >
      <!--列表-->
      <div class="assort">
        <div class="assFU">
          <router-link tag="a" :to="'/classify/goods/'+item.typeId" class="mui-active mui-ellipsis" v-for="(item,key) in list"  :key="key">
            {{item.typeName}}
          </router-link>
        </div>
      </div>
      <!--展示-->
      <div class="assortconter mui-slider-group" id="zhanshi">
        <router-view :key="key"></router-view>
      </div>
    </div>
  </div>
</template>

<script type="es6">
  import Api from '../commom/API.js';
  export default{
    data(){
      return {
        msg:'bwb',
        n:0,
        list:[]
      }
    },
    beforeCreate () {
      document.querySelector('body').setAttribute('style', 'background:#fff')
    },
    beforeDestroy () {
      document.querySelector('body').setAttribute('style', '')
    },
    mounted(){
      this.assortlist();
    },
    // 给router-view添加key使其子路由可以刷新
    computed: {
      key() {
        return this.$route.name !== undefined? this.$route.name + +new Date(): this.$route + +new Date()
      }
    },
    methods: {
      assortlist(){
        var url = Api.Apis + '/h5/h5goodtype/findOneLevelGoodType'
        this.$http.post(url).then(function (data) {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body;
            this.list = datas.data.typeBeanList;

          }
        })
      }
    }
  }
</script>

<style scoped>
  .blanks{
    position: fixed;
    width: 100%;
    height: 100%;
    background: #fff;
  }
  .seeker{
    display: flex;
    justify-content: center;
    align-items: center;
    width:100%;
    height:0.64rem;
    background: #e5e5e5;
    border:1px solid #999;
    border-radius: 2rem;
  }
  .seeker span.mui-icon.mui-icon-search{
    width: 0.32rem;
    height: 0.32rem;
    margin: 0;
    padding: 0;
    font-size: 0.3rem;
    line-height: 0.32rem;
  }
  .seeker p{
    padding-left: 0.05rem;
    font-size:0.28rem;
    height: 0.64rem;
    color: #2c3e50;
    line-height: 0.64rem;
  }
  .assort{
    width:24%;
    position: fixed;
    background-color:#ffffff;
    height:100%
  }
  .classifymain{
    position: relative;
    width: 100%;
    height: 100%;
    background-color: #ffffff;
  }
  .assort .assFU{
    position: absolute;
    top:0;left:0;
    border-right:1px solid #6d6d72;
    width:100%;
    padding:0.3rem 0 0.2rem 0;
  }
  .assort a{
    margin-top: .6rem;
    display: block;
    font-size: .3rem
  }
  .assortconter{
    position: absolute;
    width:76%;
    right: 0;
  }
  .router-link-active{
    color: #4595e5
  }
  .mui-bar .mui-icon {
    font-size: 15px;
    position: relative;
    z-index: 20;
    padding-top: 10px;
    padding-bottom: 10px;
  }
  .mui-bar-nav.mui-bar{
    height: 0.64rem;
    padding-left:0.6rem;
    padding-right: 0.6rem;
    margin-top: 0.2rem;
    margin-bottom:0.2rem;
  }
</style>



