\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\\<template>
  <div id="appraise">
    <div class="mask" v-if="mask">
      <div></div>
      <span>发表中...</span>
    </div>
		<div style="position:fixed;top:0;left: 0;height:0.88rem;width:100%;z-index: 999999;">
	      	<header class="mui-bar mui-bar-nav" style="box-shadow: none;">
	        	<a class="mui-icon mui-icon-left-nav mui-pull-left" @click="goback"></a>
	        	<h1 class="mui-title">评价</h1>
	        	<span class="issue" @click="uploadToServer">发布</span>
	      	</header>
    	</div>
      <div class="appraiseGroup">
        <div class="appraiseGroupDe" v-for="(item,idx) in goodsList"  :key="idx">
          <ul class="clearfix goodIntro">
            <li class="goodIntro_img">
              <img :src=item.skuPic alt="">
            </li>
            <li class="goodIntro_des">
              <h4>{{item.name}}</h4>
              <div>￥{{item.unitPrice | substr}}</div>
            </li>
          </ul>
          <ul class="choooseIcon">
            <li class="choooseIconI" @click="grade($event,idx)" :class="{active: look[idx].state=='3'}"  cur="3"><i cur="3"></i><span cur="3">好评</span></li>
            <li class="choooseIconII" @click="grade($event,idx)" :class="{active: look[idx].state=='2'}"  cur="2"><i  cur="2" ></i><span cur="2">中评</span></li>
            <li class="choooseIconIII" @click="grade($event,idx)" :class="{active: look[idx].state== '1'}" cur="1"><i  cur="1"></i><span cur="1" >差评</span></li>
          </ul>
          <textarea name="" class="writeIn" v-model='comment[idx].text' index="idx" onfocus="if(value=='选填：输入您的评论(不超过200字)'){value=''}"
        onblur="if (value ==''){value='选填：输入您的评论(不超过200字)'}">选填：输入您的评论(不超过200字)</textarea>
          <div class="fileIf">
            <span class="filetitle">有图有真相</span>
            <ul class="clearfix" ref="picUl">
              <li v-for="(itemx,idxs) in pics[idx]" :key="idxs" v-show="pics[idx].length>0"><img :src="itemx" alt="" :index="idxs"><span @click="decparent($event)" :index="idx" :curparent="idxs"></span></li>
              <li @click="handlerUploadUserImg(idx)" v-show="pics[idx].length<5" :index="idx"><img src="../../../static/img/my/my_comment_icon_img@2x.png" alt=""></li>
            </ul>
            <div class="spacing" v-show="idx!=count"></div>
          </div>
          <input ref="upload-input" @change="upload($event,idx)" type="file" accept="image/*" class="choose" :index="idx" multiple>
        </div>
      </div>
  	</div>
</template>
<script >
  import Vue from 'vue'
  import Api from '../commom/API.js';
  import { setCookie, getCookie } from "../commom/cookie.js";
  import { Toast } from 'mint-ui';
  export default {
    data(){
      return {
        userID: '',
        imgObjects:[],//总图片流数组
        orderid:'',
        look:[],//评分
        comment:[],//评论
        pics:[],//图片
        picsNum:[],
        orderPrice:'',
        fileObjs:[],//图片流数据
        goodsList:[],
        picTotal: [],//处理后的总图片数组
        count:-1,//间隔显示与否开关
        goodIds: [],//商品goodIds集合
        skuIds: [],//skuIds集合
        mask:false,//true
        comments:[],//评论总数组
        levels:[],//评分总数组
        imgArr:[],//返回图片数据
        i:0,
        maxSize:3072,
        imgPic:[],//只装前五张图片地址
        imgPicFile:[]//只装前五张图片流数据
      }
    },
    beforeCreate(){
    	document.querySelector('body').setAttribute('style','background:#fff')
    },
    beforeDestroy(){
    	document.querySelector('body').setAttribute('style','')
    },
    created(){
      this.orderid = this.$route.query.orids;
      Object.prototype[Symbol.iterator] = function(){
        let index = 0;
        let propKeys = Reflect.ownKeys(obj);
        return {
          next() {
            if (index < propKeys.length) {
              let key = propKeys[index];
              index++;
              return { value: [key, obj[key]] };
            } else {
              return { done: true };
            }
          }
        }
      }
    },
    mounted(){
      var _this = this
      this.getInformation(),
      this.OrderDetail()
    },
    methods: {
      OrderDetail(){
        let data = {'orderid': this.orderid};
        /*接口请求*/
        var url = Api.Apis + '/h5/h5order/myOrderDetail';
        this.$http.post(url, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
        ).then((data) => {
          let orderMsg = data.body.data.orderBean.order;
          for (let i = 0; i < data.body.data.orderBean.orderItem.length;i++){
            this.look.push([])
            this.comment.push([])
            this.pics.push([])
            this.fileObjs.push([])
            this.imgPic.push([])
            this.imgPicFile.push([])
            this.skuIds.push([(data.body.data.orderBean.orderItem)[i].skuid])
            this.goodIds.push([(data.body.data.orderBean.orderItem)[i].goodId])
            Vue.set(this.look[i], "state", 3)//评分
            Vue.set(this.comment[i], "text", '选填：输入您的评论(不超过200字)')//评论
          }
          this.count = data.body.data.orderBean.orderItem.length - 1
          this.orderPrice=orderMsg.totalPriceNow,//订单价格
          this.goodsList=data.body.data.orderBean.orderItem
        })
      },
      grade(e,index){
        var cur= e.target.getAttribute('cur')
        this.look[index].state=cur
      },
      getInformation(){//获取用户名
        this.userID = getCookie("userId");
      },
      handlerUploadUserImg(idx){//加号选中实际上传框
        this.uploadUserImg(this.$refs["upload-input"][idx])
      },
      uploadUserImg(that) {//模拟触发file的上传动作
       that.click();
      },
      upload($event,idx) {//图片上传本地
        var _this = this;
        var filesArr1 = $event.target.files
        var filesArr =[]
        var filesArrs =[]
        if (filesArr1.length > 5) {
          Toast({message: "最多同时只可上传5张图片",position: 'bottom'});
          return;
        }else{
          for (let i = 0; i < filesArr1.length; i++) {
            let file = filesArr1[i];
            let size = Math.floor(file.size / 1024);
            if (size > this.maxSize) {
              Toast({message: `文件大小不能超过${this.maxSize/1024}M`,position: 'bottom'});
              continue
            }
            filesArr.push(filesArr1[i])
          }
        }
        filesArrs=Array.prototype.slice.call(filesArr)
        for (let i = 0; i < filesArrs.length; i++) {
          var reader = new FileReader();
          var url = reader.readAsDataURL(filesArrs[i]);
          reader.onload = function(e) {
            var canvas=document.createElement("canvas");
            var ctx=canvas.getContext("2d");
            var result = this.result;
            var image = new Image();
            image.src = result;
            image.onload=function(){
              var cw=image.width;
              var ch=image.height;
              var w=image.width;
              var h=image.height;
              canvas.width=w;
              canvas.height=h;
              if(cw>400&&cw>ch){
                w=400;
                h=(400*ch)/cw;
                canvas.width=w;
                canvas.height=h;
              }
              if(ch>400&&ch>cw){
                h=400;
                w=(400*cw)/ch;
                canvas.width=w;
                canvas.height=h;
              }
              ctx.drawImage(image,0,0,w,h);
              var cropStr = canvas.toDataURL("image/jpeg",0.7);
              if(_this.pics[idx].length<5){
                _this.pics[idx].push(cropStr);
                var fileObj = _this.dataURItoBlob(cropStr)
                _this.fileObjs[idx].push(fileObj)
              }
            }
          };
        }
		$event.target.value='';
      },
      dataURItoBlob(dataUrl){//将上传的图片转化为二进制文件
        var byteString = atob(dataUrl.split(',')[1]);
        var ab = new ArrayBuffer(byteString.length);
        var ia = new Uint8Array(ab);
        for (var i = 0; i < byteString.length; i++) {
          ia[i] = byteString.charCodeAt(i);
        }
        return new Blob([ab], { type: 'image/jpeg' });
      },
      uploadToServer() {//将图片上传到后台
        var _this = this
        _this.mask=true
        for (let i = 0; i < _this.comment.length; i++) {
          if (_this.comment[i].text == '选填：输入您的评论(不超过200字)') {
            Vue.set(_this.comment[i], 'text', '轻轻的我走了，不留下只言片语')
          }
          _this.comments.push(_this.comment[i].text)
        }
        for (let i = 0; i < _this.look.length; i++) {
          _this.levels.push(_this.look[i].state)
        }
        for (let i = 0; i < _this.pics.length; i++) {
            _this.picsNum.push(_this.pics[i].length)
          for (let j = 0; j < _this.pics[i].length; j++) {
              _this.picTotal.push(_this.pics[i][j])
          }
        }
        for (let i = 0; i < _this.fileObjs.length; i++) {
          for (let j = 0; j <_this.fileObjs[i].length; j++) {
              _this.imgObjects.push(_this.fileObjs[i][j])
          }
        }
        _this.uploadToD()
      },
      uploadToD() {//用于递归的上传函数
        var _this=this
        var formData = new FormData()
        if(_this.imgObjects.length==0){
          var imgBlank=',,'
          var dataTot={
            "goodIds":_this.goodIds.join(','),
            "orderId":_this.orderid,
            "userId":_this.userID,
            "commentTexts":_this.comments.join('@@@'),
            "levels":_this.levels.join(','),
            "skuIds": _this.skuIds.join(','),
            "picNums": _this.picsNum.join(','),
            "img": _this.imgBlank
          }
          this.$http.post('https://wx.yukicomic.com/h5/h5goodcomment/xcxdoComment', dataTot,{
            emulateJSON: true
          }).then(data => {
            _this.$router.push('/myorder')
          });
        }else{
          formData.append("img", _this.imgObjects[_this.i])
          this.$http.post('https://wx.yukicomic.com/h5/h5goodcomment/xcxupload', formData,{
            emulateJSON: true
          }).then(data => {
            _this.imgArr.push(data.body.data)
            _this.i++
            if(_this.i==_this.imgObjects.length){
              var dataTot={
                "goodIds":_this.goodIds.join(','),
                "orderId":_this.orderid,
                "userId":_this.userID,
                "commentTexts":_this.comments.join('@@@'),
                "levels":_this.levels.join(','),
                "skuIds": _this.skuIds.join(','),
                "picNums": _this.picsNum.join(','),
                "img": _this.imgArr.join(',')
              }
              this.$http.post('https://wx.yukicomic.com/h5/h5goodcomment/xcxdoComment', dataTot,{
                emulateJSON: true
              }).then(data => {
                _this.$router.push('/myorder')
              });
            }else{
              this.uploadToD()
            }
          });
        }
      },
      //删除本地图片，重新选择上传
      decparent($event){
        var indexs=$event.target.getAttribute('index')
        var curparent=$event.target.getAttribute('curparent')
        this.pics[indexs].splice(curparent,1)
        this.fileObjs[indexs].splice(curparent,1)
      },
      goback(){
       this.$router.go(-1);
      },

    }
  }

</script>
<style  scoped>
  .clearfix:after { content: ""; display: block; clear: both; }
  .clearfix { zoom: 1; }
  img{
  	width: 100%;
  	height: 100%;
  }
  #appraise{
    background: #fff !important;
  }
   .mui-bar.mui-bar-nav{
   	position: relative;
   }
  .mui-bar.mui-bar-nav,.appraiseNew,.appraise_header ul li{
    background-color: #e5e5e5;
  }
  .mui-bar .mui-title{
    font-size: 0.44rem;
  }
  a.mui-icon{
    color: #007aff;
  }
  .goodIntro{
  	height: 1.68rem;
  	padding: 0.24rem 0.32rem;
  	margin-top: 0.88rem;
  	background: #fafafa;
  }
  .goodIntro li{
    text-align: left;
  	float: left;
  }
  .goodIntro_img{
  	width: 1.2rem;
  	height: 1.2rem;
  	/*padding: 0.72rem 0.46rem;*/
  	margin-right: 0.24rem;
  }
  .goodIntro_des{
  	position: relative;
  	width: 5.42rem;
  	height: 1.2rem;
  }
  .goodIntro_des h4{
  	font-size: 0.26rem;
  	color: #333;
  	line-height: 0.32rem;
  }
  .goodIntro_des div{
  	position: absolute;
  	bottom: 0;
  	font-size: 0.26rem;
  	color: #e54560;
  }
  .choooseIcon{
  	height: 1.28rem;
  	width: 100%;
  	display: -webkit-flex;
  	display: -moz-flex;
  	display: -ms-flex;
  	display: flex;
  }
  .choooseIcon li{
  	flex: 1;
  	display: flex;
  	justify-content:center;
  	align-items:center;
  }
  .choooseIcon li i,.choooseIcon li span{
  	display: inline-block;
  }
  .choooseIcon li i{
  	width: 0.96rem;
  	height: 0.96rem;
  	margin-right: 0.24rem;
  }
  .choooseIcon li span{
  	font-size: 0.28rem;
  	color: #666;
  }
  .choooseIcon li.choooseIconI.active span{
    color: #e54560;
  }
  .choooseIcon li.choooseIconII.active span{
    color: #ff9600;
  }
  .choooseIcon li.choooseIconII.active span{
    color: #000;
  }
  .choooseIconI i{
  	background: url(../../../static/img/my/my_comment_icon_good_n@2x.png);
  	background-size: 100% 100%;
  }
  .choooseIconI.active i{
  	background: url(../../../static/img/my/my_comment_icon_good_p@2x.png);
  	background-size: 100% 100%;
  }
  .choooseIconII i{
  	background: url(../../../static/img/my/my_comment_icon_medium_n@2x.png);
  	background-size: 100% 100%;
  }
  .choooseIconII.active i{
  	background: url(../../../static/img/my/my_comment_icon_medium_p@2x.png);
  	background-size: 100% 100%;
  }
  .choooseIconIII i{
  	background: url(../../../static/img/my/my_comment_icon_bad_n@2x.png);
  	background-size: 100% 100%;
  }
  .choooseIconIII.active i{
  	background: url(../../../static/img/my/my_comment_icon_bad_p@2x.png);
  	background-size: 100% 100%;
  }
  .writeIn{
  	width: 6.86rem;
  	height: 3rem;
  	margin-bottom: 0.24rem;
  	padding: 0.48rem;
  	background: #fafafa;
  	border-radius: 6px;
  	resize: none;
  	overflow-y:auto;
  	font-size: 0.32rem;
  	color: #ccc;
  	line-height: 0.4rem;
  }
  .filetitle{
  	display: block;
  	font-size: 0.44rem;
  	height: 0.92rem;
    padding-left: 0.32rem;
    padding-right: 0.32rem;
  	text-align: left;
  	height: 0.92rem;
  	line-height: 0.92rem;
  }
  .issue{
  	position: absolute;
  	top: 0;
  	right: 10px;
  	height: 0.88rem;
  	color: #f47920;
  	font-size: 0.44rem;
  	line-height: 0.88rem;
  }
  .fileIf ul{
    padding-left: 0.09rem;
    padding-right: 0.09rem;
  }
  .fileIf ul li{
    position: relative;
    height: 1rem;
    width: 1rem;
    float: left;
    margin-left: 0.23rem;
    margin-right:0.23rem;
    margin-bottom: 0.23rem;
    background: #fafafa;
    border: 1px dashed #d9d9d9;
  }
  .fileIf ul li span{
    position: absolute;
    width: 0.4rem;
    height: 0.4rem;
    top: -0.2rem;
    right: -0.2rem;
    background: url(../../../static/img/my/my_comment_icon_delete@2x.png);
    background-size: 100% 100%;
  }
  .choose{
    display: none;
  }
  .spacing{
    height: 0.3rem;
    background:#eee;
  }
  .mask{
    position: fixed;
    top:0;
    left:0;
    background:#000;
    opacity: 0.6;
    width:100%;
    height: 100%;
    z-index: 99;
  }
  .mask div,.mask span{
    position: fixed;
    left:50%;
    top:50%;
    margin-left:-3rem;
    margin-top:-3rem;
    width:6rem;
    height: 6rem;
    font-size:0.35rem;
    color:#fff;
    text-align: center;
    line-height: 6rem;
  }
  .mask div{
    background:url(../../../static/img/loading.gif);
    background-size: 100% 100%;
  }
</style>





