<template>
  <!-- 头部返回组件-->
  <!-- 列表部分开始-->
    <div id="news_recommend">
      <!--<top_back></top_back>-->
      <header id="header" class="mui-bar mui-bar-nav" style="background: #fff">
        <h2 class="mui-title">{{title}}</h2>
        <a class="mui-icon mui-icon-left-nav mui-pull-left" @click="goback" style="color: #4595e5"></a>
      </header>

      <div class="page-loadmore mui-content">
        <div class="page-loadmore-wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
          <mt-loadmore :bottom-method="loadBottom" @bottom-status-change="handleBottomChange" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="false">

            <div class="news_main mui-content">
              <div class="mui-content collMain">
                <div class="collectShop" v-for="(item,index) in list">
                  <router-link tag="div" :to="{path:'details',query:{ids:item.goodId}}" class="neirong">
                    <img :src="item.goodPicUrl">
                    <p class="biaoti">{{item.goodName}}</p>
                    <p class="goodsPrice">￥{{item.goodMinPrice | substr}}</p>
                  </router-link>
                </div>
              </div>
            </div>

          </mt-loadmore>
        </div>
      </div>

    </div>
  <!-- 列表部分结束-->
</template>
<script type='es6'>
  import top_back from "../commom/top_back.vue";
  import Api from '../commom/API.js';
  import Vue from 'vue';
     export default {
     data(){
         return {
           scrollMode:"auto",
           list:[],
           indenx:0,
           isNews:this.$route.query.isNews,
           newList:[],
           title:'',
           allLoaded: false,
           bottomStatus: '',
           wrapperHeight: 0,
           //wrapperHeight: 0,
           translate: 0,
           moveTranslate: 0,
           lastValue:1,
           maxIndex:''
         }
     },
       components:{
         top_back,
       },
       mounted(){
         this.getgoodlist();
         this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
       },
       methods:{
         goback(){
           this.$router.go(-1)
         },
         getgoodlist(){
           //新品推荐
           if(this.isNews=='true'){
             this.title = '新品推荐';
             var url = Api.Apis+"/h5/h5good/findNewGoodList";
             this.$http.post(url).then(function(data){
               var datas =  data.body;
               this.list = datas.data.newGoodBeanList;
               this.allLoaded = true;
             })
           //  人气推荐
           }else if(this.isNews==='false'){
             this.title = '人气推荐';
             var url = Api.Apis+"/h5/h5good/goodList";
             this.$http.post(url,
               {oneLevelTypeId:0,twoLevelTypeId:0,index:this.indenx,size:10},
               {emulateJSON: true}).then(function(data){
                 if(data.status===200&&data.statusText==="OK"){
                   var datas = data.body;
                   this.maxIndex = datas.data.maxIndex;
                   if(this.maxIndex == 1){
                     this.allLoaded = true;
                   }
                   this.list = datas.data.goodBeanList;
                 }
             }, function (error) {
               alert("网络错误")
               })
           }
         },
         //下拉加载
         handleBottomChange(status) {
           this.bottomStatus = status;
         },
         loadBottom() {
           setTimeout(() => {
             if (this.lastValue < this.maxIndex) {
               var thats = this;
               let data = {'index': thats.lastValue,'oneLevelTypeId':0,'twoLevelTypeId':0,'size':10};
               /*接口请求*/
               var url = Api.Apis + '/h5/h5good/goodList';
               this.$http.post(url, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
               ).then((data) => {
                 if (data.status == 200 && data.ok == true) {
                   var datas = data.body.data;
                   if (datas.state == "success") {
                     datas.goodBeanList.forEach((value)=>{thats.list.push(value)});
                     thats.lastValue ++ ;
                   }
                 }
               });
             } else {
               this.allLoaded = true;
             }
             this.$refs.loadmore.onBottomLoaded();
           }, 1500);
         }
       },
     }
</script>
<style scoped>
  .collMain{width:100%;margin-top: 0.2rem;overflow: hidden;padding-left: .22rem}
  .collectShop{margin:0 .2rem .2rem 0   ;float:left;width:47%;position: relative;padding: 0.1rem 0.2rem 0.1rem .2rem;background: #fff;}
  .neirong, .neirong img{width:100%}
  .neirong img{height:3rem}
  .biaoti{font-size: .3rem;text-align: left;width:100%;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}
  .goodsPrice{color:red;text-align: left;font-size: .3rem;margin-top: .1rem}
  .mint-loadmore-top span {
    -webkit-transition: .2s linear;
    transition: .2s linear
  }
  .mint-loadmore-top span {
    display: inline-block;
    vertical-align: middle
  }
  .page-loadmore-wrapper {
    overflow: scroll
  }
  .mint-loadmore-bottom span {
    display: inline-block;
    -webkit-transition: .2s linear;
    transition: .2s linear;
    vertical-align: middle
  }
</style>



