<template>
  <div id="seekd">
     <header id="header" class="mui-bar mui-bar-nav" style="background:#fff;box-shadow: none">
      <a class="mui-btn mui-btn-blue mui-btn-link mui-pull-right" @click="seekGoods">搜索</a>
      <div class="search">
        <span class="mui-icon mui-icon-search"></span>
        <input type="text" class="to_search" value="日本进口面膜" v-model="goodsName" @focus="ipt=''" @blur="methBlur($event)" @keyup="textF($event)"/>
      </div>
      <a class="mui-icon mui-icon-left-nav mui-pull-left" @click.stop="goBack" style="color:#4595e5"></a>
    </header>
    <div class="page-loadmore mui-content">
      <div class="page-loadmore-wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
        <mt-loadmore :bottom-method="loadBottom" @bottom-status-change="handleBottomChange" :bottom-all-loaded="allLoaded" ref="loadmore" :auto-fill="false">

          <div class="orderList-null" v-show="!list.length">
            <div class="orderList-null-icon"></div>
            <div class="orderList-null-title">您搜索的商品还未上架，敬请期待！</div>
          </div>
          <div class="news_main" v-show="list.length">
            <!--<p v-show="!list.length" style="background: #fff;margin:0 auto">您搜索的商品还未上架，敬请期待！</p>-->
            <ul class="mui-table-view mui-grid-view" style="margin: 0;text-align: left">
              <li class="mui-table-view-cell mui-media mui-col-xs-6" v-for="(item,indenx) in list">
                <router-link :to="{path:'details',query:{ids:item.goodId}}" >
                  <img class="mui-media-object" :src=item.goodPicUrl>
                  <div class="mui-media-body">{{item.goodName}}</div>
                  <p class="news_main_prices">￥{{item.goodMinPrice|substr}}</p>
                </router-link>
              </li>
            </ul>
          </div>

        </mt-loadmore>
      </div>
    </div>
  </div>
</template>

<script type='es6'>
  import Api from '../commom/API.js';
  import Vue from 'vue';
  import { Toast } from 'mint-ui';
  export default {
    data(){
      return {
        conTab:true,
        scrollMode:"auto",
        goodsName:this.$route.query.gN,
        list:[],
        indenx:0,
        newList:[],
        titleName:'',
        liShi:[],
        tipShow:false,
        allLoaded: false,
        bottomStatus: '',
        wrapperHeight: 0,
        //wrapperHeight: 0,
        translate: 0,
        moveTranslate: 0,
        lastValue:1,
        maxIndex:''
      }
    },
    mounted(){
      this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top;
      this.getgoodsName();

    },
    methods:{
      goBack(){
          this.$router.go(-1);
      },
      getgoodsName(){
        let url = Api.Apis + '/h5/h5search/searchList';
        let data = {'goodName': this.goodsName,'index':0,'size':10};
        this.liShi = JSON.parse(localStorage.getItem('hN'));
        // 写入localStorage
        if(this.liShi==null){
          // this.liShi必须为数组。否则push方法报错。
            this.liShi=[];
        }
        this.liShi.push(this.goodsName);
        window.localStorage.setItem('hN',JSON.stringify(this.liShi));
        this.$http.post(url, data, {emulateJSON: true}
        ).then(function (data) {
          if (data.status == 200 && data.ok == true) {
            var datas = data.body.data;
            if (datas.state == "success") {
              this.list = datas.goodBeanList;
              console.log(datas);
              this.maxIndex = datas.maxIndex;
              if(this.maxIndex == 1){
                this.allLoaded = true;
              }
            } else {
              Toast({message: datas.errMsg,position: 'bottom'});
            }
          }
        })
      },
      seekGoods(){
        var _this=this
        if(this.conTab){
          this.getgoodsName();
          this.conTab=false;
          setTimeout(function(){
            _this.conTab=true
          },2000)
        }else{
          Toast({message: '亲，您点击的频率太高了，稍微休息会儿吧',position: 'bottom'});
        }
      },
      methBlur(event){
        this.goodsName==''?this.goodsName=this.$route.query.gN:this.goodsName=event.currentTarget.value
      },
      textF(event){
        if(event.currentTarget.value.replace(/(^[a-zA-Z0-9\u4e00-\u9fa5]+$)/g,'')){
          Toast({message: '只支持中文英文数字输入',position: 'bottom'});
          this.goodsName=this.$route.query.gN
        }
      },
      //下拉加载
      handleBottomChange(status) {
        this.bottomStatus = status;
      },
      loadBottom() {
        setTimeout(() => {
          if (this.lastValue < this.maxIndex) {
            var thats = this;
            let data = {'index': thats.lastValue,'size':10,'goodName': thats.goodsName,};
            /*接口请求*/
            var url = Api.Apis + '/h5/h5search/searchList';
            this.$http.post(url, data, {emulateJSON: true}//POST请求需要将emulateJSON设置为true
            ).then((data) => {
              if (data.status == 200 && data.ok == true) {
                var datas = data.body.data;
                if (datas.state == "success") {
                  datas.goodBeanList.forEach((value)=>{thats.list.push(value)});
                  thats.lastValue ++ ;
                }
              }
            });
          } else {
            this.allLoaded = true;
          }
          this.$refs.loadmore.onBottomLoaded();
        }, 1500);
      }
    },
  }
</script>
<style type="text/css" scoped>
  .search{
    position: absolute;
    top: 50%;
    left: 50%;
    padding-left: 0.3rem;
    margin-left: -35%;
    margin-top: -0.32rem;
    overflow: hidden;
    width: 70%;
    height: 0.64rem;
    border: 1px solid #f0f0f0;
    border-radius: 0.32rem;
    background: #fff;
    }
    .search .mui-icon,.search input{
      background: #EBEBEB;
    }
  .mui-icon-search{
      position: absolute;
      font-size: 0.3rem;
      left: 0;
      top: -10%;
      display: inline-flex;
      margin-left: 0.16rem;
      width: 0.32rem;
      height: 0.32rem;
      background-size: 100%;
  }
 .search input{
    padding: 0;
    padding-left: 0.4rem;
    font-size: 0.28rem;
    outline: none;
    border: none;
    width: 100%;
    height: 100%;
    margin: 0;
   background: #fff;
  }
  .mui-content{
    background: #fff;
    height:100%
  }
  .header{
    position: relative;
    height: 0.90rem;
    padding: 0.11rem 0.2rem;
  }
  .header a{
    position: absolute;
    left: 0.2rem;
    top: 50%;
    width: 0.48rem;
    height: 0.48rem;
    line-height: 0.48rem;
    margin-top: -0.24rem;
    margin-left: 0;
    color: #4595e5;
    background: #fff;
  }
  .header i{
    float: right;
    width: 10%;
    padding: 0;
    height: 0.68rem;
    font-size: 0.32rem;
    line-height: 0.68rem;
    text-align: center;
    font-style: normal;
    color: #007aff;
  }
  .mui-icon.mui-icon-search{
    position: absolute;
    width: 0.36rem;
    height: 0.36rem;
    top: 20%;
    left: 0.1rem;
    margin: 0;
    margin-top: -0.18rem;
    line-height: 0.36rem;
    background: #fff;
  }
  .mint-loadmore-top span {
    -webkit-transition: .2s linear;
    transition: .2s linear
  }
  .mint-loadmore-top span {
    display: inline-block;
    vertical-align: middle
  }
  .page-loadmore-wrapper {
    overflow: scroll
  }
  .mint-loadmore-bottom span {
    display: inline-block;
    -webkit-transition: .2s linear;
    transition: .2s linear;
    vertical-align: middle
  }
</style>
<style lang='less' scoped>
  #seekd{
    .news_main{
      ul{
        margin: 0;
        padding: 0;
        background-color: #fff;
        text-align: left;
        li{
          padding: 15px;
          padding-right: 5px;
          text-align: left;
          .news_main_prices{
            font-size: 0.29rem;
            color: red;
          }
          img{
            height:2.6rem;
          }
          div{
            height: 0.3rem;
            line-height: 0.3rem;
            font-size: 0.28rem;
          }
          a{
            background-color: #ffffff;
          }
        }
      }
    }
    .orderList-null {
      width: 6rem;
      margin: 0 auto;
      padding-top: 4rem;
    }
    .orderList-null .orderList-null-icon {
      width: 3rem;
      height: 3rem;
      background: url("../../../static/img/default-page/common@3x.png") no-repeat;
      background-size: 100%;
      margin: 0 auto;
    }
    .orderList-null .orderList-null-title {
      width: 100%;
      margin-top: 0.2rem;
      font-size: 0.28rem;
      line-height: 0.28rem;
      color: #666;
      text-align: center;
    }

  }
</style>




